import NextAuth from "next-auth";
import GoogleProvider from "next-auth/providers/google";
import jwt from "jsonwebtoken";
import { NextAuthOptions } from "next-auth";

declare module "next-auth" {
  interface Session {
    user: {
      id?: string;
      jwt?: string;
      name?: string | null;
      email?: string | null;
      image?: string | null;
    };
  }
}

declare module "next-auth/jwt" {
  interface JWT {
    uid?: string;
    customJWT?: string;
  }
}

export const authOptions: NextAuthOptions = {
  providers: [
    GoogleProvider({
      clientId: process.env.NEXT_APP_GOOGLE_AUTH_CLIENT_ID!,
      clientSecret: process.env.NEXT_APP_GOOGLE_AUTH_CLIENT_SECRET!,
    }),
  ],
  session: {
    strategy: "jwt",
  },
  callbacks: {
    async session({ session, token }) {
      console.log("session", session);

      if (session.user) {
        session.user.id = token.uid;
      }
      session.user.jwt = token.customJWT;

      // Add some logging to check what's being returned in the session
      console.log("Session Callback:", session);

      return session;
    },

    async jwt({ token, user }) {
      // On first login, generate a JWT with user details
      if (user) {
        console.log("User on first login:", user);

        token.uid = user.id;
        token.customJWT = jwt.sign(
          { id: user.id, email: user.email },
          process.env.JWT_SECRET!,
          { expiresIn: "7d" }
        );
      }

      console.log("JWT Callback:", token);

      return token;
    },
  },
};

const handler = NextAuth(authOptions);
export { handler as GET, handler as POST };
